
import CameraAltIcon from '@material-ui/icons/CameraAlt';
import CodeIcon from '@material-ui/icons/Code';
import MovieIcon from '@material-ui/icons/Movie';
import { useEffect } from 'react';
// import { useState } from 'react';
import { Link } from 'react-router-dom'
import recruit from '../recruit.png';
import social from '../social.png';
import './home.component.scss'

import me from '../assets/me.jpg';
export default function Home() {


    useEffect(() => {

        setTimeout(() => {
            let reveal1 = document.querySelector('.App-header');
            // for (let i = 0; i < reveal1.length; i++) {
            reveal1.classList.add('active1');
            reveal1.classList.add('revealer');
            // }
        }, 600);
        const revealerpoint = 10;
        document.getElementById('App').addEventListener('scroll', () => {
            console.log("scrolling");
            let revealers = document.querySelectorAll('.revealer');
            for (let i = 0; i < revealers.length; i++) {
                let windowheight = window.innerHeight;
                let revealertop = revealers[i].getBoundingClientRect().top;
                let revealerbottom = revealers[i].getBoundingClientRect().bottom;
                if (revealertop < windowheight - revealerpoint) {
                    revealers[i].classList.add('active1')
                } else {
                    revealers[i].classList.remove('active1');
                };
                if (revealerbottom < 0 + revealerpoint) {
                    revealers[i].classList.remove('active1');
                }
            }
        });
    });
    return (


        <div>


            <div className="App " id="App">

                <div className="App-header">


                    <p className="profile revealme fromright">

                        <p className="icon-group ">
                            <CameraAltIcon className="icon icon-cam"></CameraAltIcon>
                            <CodeIcon className="icon icon-code"></CodeIcon>
                            <MovieIcon className="icon icon-movie"></MovieIcon>
                        </p>

                        <img className="App-logo revealme fromright" src={me} alt="Logo" />
                        <h1 className="revealme fromleft"> Vaisakh M A </h1>
                        <p className="revealme fromright"> <span >Hi, I'm a   </span>

                            <span class="toBeReplaced"> </span>
                        </p>
                    </p>
                    {/* class={`entry-buttons item ${action ? 'active' : 'active'}`} */}
                </div>


                <div class='entry-buttons revealer'>

                <div class="options revealme fromleft">
                            <Link to="/resume">
                                <p>
                                    <img alt="recruit" src={recruit}></img>
                                </p>
                                <p> You're a Recruiter</p>
                            </Link>

                        </div>
                    <div class="options revealme fromright"> <p> <img alt="social" src={social}></img>   </p>  <p> You're a Follower</p></div>

                </div>
            </div>
        </div>

    )
}
